import Landing from './../../components/home/index.vue'
import WhyCalculator from './../../components/why-calculator/index.vue'
import Features from './../../components/features/index.vue'
import Pricing from './../../components/pricing/index.vue'
import Examples from './../../components/examples/index.vue'

const routes = [
	{ path: '/', component: Landing, name: 'home' },
	{ path: '/why', component: WhyCalculator, name: 'whyCalculator' },
	{ path: '/features', component: Features, name: 'features' },
	{ path: '/pricing', component: Pricing, name: 'pricing' },
	{ path: '/examples', component: Examples, name: 'examples' },
]

export default routes


