import store from './assets.store'

export default {

	head: document.getElementsByTagName('head')[0],

	body: document.getElementsByTagName('body')[0],

	loadComponentAssets (component, type, removeNonCompAssets = false, attachTo = 'head') {

		if (removeNonCompAssets)
			this.removeAssetsExcluding(component, type)

		if ('css' === type) {
			let styles = store.css[component];

			if (styles) {
				for (let name in styles) {
					let elem = document.createElement('link')
					elem.rel = 'stylesheet'
					elem.href = styles[name]
					elem.id = component+'-css-'+name

					this[attachTo].append(elem)
				}
			}
		}

		if ('js' === type) {
			let scripts = store.js[component]

			if (scripts) {

				for (let name in scripts) {
					let elem = document.createElement('script')
					elem.src = scripts[name]
					elem.type = "text/javascript"
					elem.id = component+'-js-'+name

					this[attachTo].append(elem)
				}
			}
		}
	},

	removeAssetsExcluding (componentName, type) {

		if ('css' === type) {
			let components = store.css

				for (let component in components) {

				if ('common' !== component && component !== componentName) {

					let styles = store.css[component]

					for (let name in styles) {
						let lookUpID = component+'-css-'+name
						let lookUpElem = document.getElementById(lookUpID)
						if (lookUpElem)
							lookUpElem.remove();
					}
				}
			}
		}

		if ('js' === type) {
			let components = store.js

			for (let component in components) {

				if ('common' !== component && component !== componentName) {

					let scripts = store.js[component]

					for (let name in scripts) {
						let lookUpID = component+'-js-'+name
						let lookUpElem = document.getElementById(lookUpID)
						if (lookUpElem)
							lookUpElem.remove();
					}
				}
			}
		}
	}
}


