<template>
	<div :id="routeID">
		<navbar></navbar>
		<transition name="fade">
			<router-view></router-view>
		</transition>
		<footer-bar></footer-bar>
	</div>
</template>

<script>
	import Navbar from './navbar/index.vue'
	import Footer from './footer/index.vue'
	import AssetService from './../shared/services/assets/assets.service'

	export default {

		components: {
			'navbar': Navbar,
			'footer-bar': Footer
		},

		created: function () {
			this.loadComponentDep();
		},

		computed: {
			routeID () {
				switch(this.$route.name) {
					case 'whyCalculator':
						return 'why'
						break
					case 'features':
						return 'feature'
						break
					case 'pricing':
						return 'pricing'
						break
					case 'examples':
						return 'examples'
						break
					default:
						return '';
				}
			}
		},

		methods : {

			loadComponentDep () {
				AssetService.loadComponentAssets('common', 'js');
			}
		}
	}
</script>

<style>
    .fade-enter-active, .fade-leave-active {
      transition: opacity 0.8s
    }
    .fade-enter, .fade-leave-to /* .fade-leave-active in <2.1.8 */ {
      opacity: 0
    }
</style>


