<template>
	<!-- Homepage header start-->
	<nav class="navbar custom-navbar navbar-default navbar-fixed-top">
		<div class="container-fluid">
			<!-- Brand and toggle get grouped for better mobile display -->
			<div class="navbar-header">
				<button type="button" class="navbar-toggle collapsed" data-toggle="collapse" data-target="#bs-example-navbar-collapse-1" aria-expanded="false">
					<span class="sr-only">Toggle navigation</span>
					<span class="icon-bar"></span>
					<span class="icon-bar"></span>
					<span class="icon-bar"></span>
				</button>
				<router-link class="navbar-brand params" to="/"><img src="/assets/images/logo.png" /></router-link>
			</div>

			<div class="collapse navbar-collapse" id="bs-example-navbar-collapse-1">
				<ul class="nav navbar-nav navbar-right">
                    <li class="">
                    	<router-link to="/why" onclick="callGA('WHY')" class="params">Why Interactive Content?</router-link>
                	</li>
                    <li class="">
                    	<router-link to="/features" onclick="callGA('FEATURES')" class="params">Features</router-link>
                	</li>
                    <li>
                    	<router-link to="/pricing" onclick="callGA('PRICING')" class="params">Pricing</router-link>
                	</li>
                    <li class="">
                    	<router-link to="/examples" class="params">Examples</router-link>
                	</li>
                	<li v-if="!isLoggedIn" class="login">
                		<a :href="loginLink" class="link-login params" onclick="callGA(LOGIN)">LOGIN</a>
                	</li>
                    <li v-if="!isLoggedIn" class="anchorParent signUp signUp-style">
                    	<a :href="signupLink" class="link-login params" onclick="callGA(SIGN UP)">SIGN UP</a>
                    </li>
                    <li v-if="isLoggedIn" class="anchorParent signUp">
                    	<a :href="dashboardLink" class="link-login params" onclick="callGA(DASHBOARD)">DASHBOARD</a>
                    </li>
                </ul>
			</div>
		</div>
	</nav>
</template>

<script>
	export default {

		computed : {
			isLoggedIn () {
				if (this.readCookie('storage')) {
					return true;
				}
				return false;
			},

			loginLink () {
				return "//app." + window.location.hostname + "/login"
			},

			signupLink () {
				return "//app." + window.location.hostname + "/signup"
			},

			dashboardLink () {
				let storage = JSON.parse(this.readCookie('storage'));
				let link = "//app." + window.location.hostname + "/dashboard"
				if (storage && storage.companyList) {
					link = "//" + storage.companyList[1] + window.location.hostname + "/dashboard"
				}
				return link;
			}
		},

		methods: {
			readCookie(name) {
				var nameEQ = name + "=";
				var ca = document.cookie.split(';');
				for(var i=0; i < ca.length; i++) {
				    var c = ca[i];
				    while (c.charAt(0)==' ') c = c.substring(1,c.length);
				    if (c.indexOf(nameEQ) == 0) return c.substring(nameEQ.length,c.length);
				}
				return null;
			}
		}
	}
</script>


